import GitHubIcon from '@mui/icons-material/GitHub';
import LinkedInIcon from '@mui/icons-material/LinkedIn'
import PhoneIcon from '@mui/icons-material/Phone';
import { about } from '../../portfolio'
import { contact } from '../../portfolio'
import './Home.css'

const Home = () => {
  const { name, role, description, resume, social } = about

  return (
    <div className='about center' id='home'>
      <img src="https://avatars.githubusercontent.com/u/93419494?v=4" alt="" className="profile-image home-img" />
      {name && (
        <h1>
          Hi, I am <span className='about__name' id='"user-detail-name"'>{name}.</span>
        </h1>
      )}

      {role && <h2 className='about__role' id='user-detail-intro'>A {role}.</h2>}
      {/* <p className='about__desc'>{description && description}</p> */}

      <div className='about__contact center'>
        {resume && (
          <a href={resume} download>
            <span type='button' className='btn btn--outline' id=' resume-button-2'>
              Resume
            </span>
          </a>
        )}

        <a href={`mailto:${contact.email}`} target="_blank" rel='noreferrer'>
          <span type='button' className='btn btn--outline' id='contact-email'>
          Email me
          </span>
        </a>
              <button
                className='link link--icon'
                id='contact-phone'
                target="_blank"
                rel='noreferrer'
              >
                <PhoneIcon />
              </button>
        {social && (
          <>
            {social.github && (
              <a
                href={social.github}
                aria-label='github'
                className='link link--icon'
                id='contact-github'
                target="_blank"
                rel='noreferrer'
              >
                <GitHubIcon />
              </a>
            )}

            {social.linkedin && (
              <a
                href={social.linkedin}
                aria-label='linkedin'
                className='link link--icon'
                id='contact-linkedin'
                target="_blank"
                rel='noreferrer'
              >
                <LinkedInIcon />
              </a>
            )}
          </>
        )}
      </div>
    </div>
  )
}

export default Home
